import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Reply from './reply';
import editReply from './reply/edit'

import Group from './group';
import editGroup from './group/edit';

class SourceWrapper extends Component {
  render() {
    return (
        <Route path="/admin/source" render={() =>
          <Switch>
            {/*图文素材*/}
            <Route path='/admin/source/group'>
              <Switch>
                <Route path="/admin/source/group/index" component={Group} />
                <Route path='/admin/source/group/create' component={editGroup} />
                <Route path='/admin/source/group/update' component={editGroup} />
                <Redirect from="/admin/source/group" exact to='/admin/source/group/index' />
              </Switch>
            </Route>
            {/*回复素材*/}
            <Route path='/admin/source/reply'>
              <Switch>
                <Route path="/admin/source/reply/index" component={Reply} />
                <Route path='/admin/source/reply/create' component={editReply} />
                <Route path='/admin/source/reply/update' component={editReply} />
                <Redirect from="/admin/source/reply" exact to='/admin/source/reply/index' />
              </Switch>
            </Route>
            <Redirect from="/admin/source" exact to='/admin/source/reply/' />
          </Switch>
        } />
    )
  }
}

export default SourceWrapper;