import React from 'react';
import { Radio, Row, Col, Modal, Button } from 'antd';
import {connect} from 'react-redux';
import {actionCreators} from '../store';

const SelectGonghao = props => {
  const {selectGonghaoModal, allCodes, messageInfo, confirmSelect, closeSelectModal} = props;
  return (
    <Modal width={1000} visible={selectGonghaoModal} maskClosable={false} closable={false} mask={false}  footer={<Button type="primary" disabled={!messageInfo.code} onClick={closeSelectModal}>确定</Button>}>
      <Radio.Group value={messageInfo.code} style={{ width: '100%' }} onChange={e => confirmSelect(e.target.value, messageInfo.code)}>
        <Row>
          {allCodes.map(item => <Col span={4} key={item._id}>
            <Radio value={item.code}>{item.nick_name}</Radio>
          </Col>)}
        </Row>
      </Radio.Group>
    </Modal>
  )
};

const mapState = state => ({
  messageInfo: state.message.template.messageInfo
});

const mapDispatch = dispatch => ({
  // changeMessageInfo(e) {
  //   let code = e.target.value;
  //   dispatch(actionCreators.changeMessageInfo("code", code))
  // },
  confirmSelect(code) {
    dispatch(actionCreators.confirmSelect(code))
  },
  closeSelectModal() {
    dispatch(actionCreators.closeSelectModal())
  }
});

export default connect(mapState, mapDispatch)(SelectGonghao);